.to{
  height: 100%;
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
}

body {
  font-family: 'Montserrat', sans-serif;
}

.app {
  background-image: url('./photos/cold-bg8.jpg');
  background-size: cover;
  background-position: center;
  transition: 0.4 initial;
  background-repeat: no-repeat;

}

.app.warm {
  background-image: url('./photos/warm2-bg.jpg');


}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  margin: 0 0 90px;
  width: 100%;
  height: 100px;
  border-radius: 8px;
}

.search-box .search-bar {

  display: block;
  width: 60%;
  padding: 15px;
  margin: 0 auto;
  display: center;

  appearance: none;
  background: none;
  outline: none;
  border: none;
  
  background-color: rgba(225, 225, 225, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  
  color: #424242;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #FFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 40px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.time {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.Clouds {
  width: 120px;
  border: 1px solid #ddd;
  border-radius: 20%;
  padding: 5px;
  height: 110px;
  opacity: 0.8;
}
.Rain{
  width: 120px;
  border: 1px solid #ddd;
  border-radius: 20%;
  padding: 5px;
  opacity: 0.8;
  height: 110px;
}
.Clear{
  width: 120px;
  height: 110px;
  border: 1px solid #ddd;
  border-radius: 20%;
  padding: 5px;
  opacity: 0.8;
}